.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.App__header {
    height: 40px;
    padding: 4px 4px;
}

.App__content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.App__table {
    height: 100%;
}

.App__file-btn {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.App__file-btn + label {
    font-size: 1.25em;
    font-weight: 700;
    color: #111;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    display: inline-block;
}

.App__file-btn + label:hover {
    background-color: #ddd;
}


